#madmax-container{
    display:flex;
    justify-content: center;
    align-items: center;
    background-image: url("/toybox/madmaxbg.png");
    background-size: cover;
    background-attachment: fixed;
    box-sizing: border-box;
    height: 100vh;
}

#madmax-main{
    position: absolute;
    width: 90vw;
    height: 80vh;
    background-color:rgb(144, 126, 187);
}

#madmax-box::-webkit-scrollbar {
    display: none;
}

#madmax-box{
    display:flex;
    height:100%;
    -ms-overflow-style: none;
    scrollbar-width: none;
    overflow: scroll;
}

#madmax-content{
    min-width: 80 * 5vw;
    min-height: 100%;
}

#madmax-sky{
    height: 70%;
    #madmax-sky1{
        height: 100%;
        background: linear-gradient(0deg, rgba(64,71,64,1) 0%, rgba(165,163,165,1) 31%, rgba(26,57,74,1) 62%, rgba(22,56,58,1) 100%);
    }
}

#madmax-ground{
    height: 30%;
    #madmax-ground1{
        height: 100%;
        background: radial-gradient(circle, rgba(226,120,18,1) 0%, rgba(92,42,9,1) 87%);
    }
    
}

#scrollCar{
    position: absolute;
    bottom: 0px;
    width: 12.5vw;
    height: 20px;
    z-index: 10;
    transition: 0.1s ease;
    animation: bounce 0.3s linear infinite alternate;
    img{
        width: 12.5vw;
        height: 20px;
    }
}

#car1{
    position: absolute;
    top: 40%;
    left: 40%;
    animation: boom 0.3s linear 10s;
    img{
        position: absolute;
        animation: bounce 0.3s linear infinite alternate;
        width: 12.5vw;
        height: 20px;
    }
}


@keyframes bounce{
    0%{bottom:2px}
    100%{bottom:-2px}
}

@keyframes boom{
    0%{}
    25%{
        transform: rotate(-180deg);
        bottom: 30%;
    }
    100%{transform: rotate(-720deg);}
}