#side-container{
    position: absolute;
    z-index: 50;
    height: 100vh;
    display:flex;
    align-items: center;
    justify-content: center;
}

#side{
    display:flex;
    justify-content: space-around;
    position: fixed;
    bottom: 2.5vh;
    width: 400px;
    background-color: gray;
}

#sideIcon{
    img{
        width: 25px;
    }
}