$sightSize: 250px;

.joker{
    min-height: 100vh;
    display:flex;
    justify-content: center;
    align-items: center;
    background-image: url("https://image.cnbcfm.com/api/v1/image/106148169-1569417005837jokercropped.jpg?v=1569417008&w=740&h=416");
    background-size: cover;
    background-position-x: -600px;
    box-sizing: border-box;
}

.joker-main{
    height:80vh;
    width: 80vw;
    cursor:none;
}

#phoenix{
    position: absolute;
    z-index: 10;
    background-image: url("/toybox/phoenix.jpg");
    background-size: cover;
    background-position: center;
    background-size: 100vw 100vh;
    height: 80vh;
    width: 80vw;
}

#sight{
    height: $sightSize;
    width: $sightSize;
    border-radius: 50%;
    background-image: url("/toybox/joker.jpg");
    background-attachment: fixed;
    background-size: 100vw 100vh;
    position: absolute;
    z-index: 10;
    transition: 0.05s;
}
